import { authAPI, securityAPI } from "../API/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const GET_CAPTCHA_URL_SUCCESS = 'auth/GET_CAPTCHA_URL_SUCCESS';

let initialState = {
  userData: {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null,
  },
  isFetching: false,
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
    case GET_CAPTCHA_URL_SUCCESS:
      return {
        ...state,
        userData: {
          ...action.userData,
        }
      }
    default: return state;
  }
}

const setUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA, userData: {
    userId, email, login, isAuth
  }
})

const getCaptchaUrlSuccess = (captchaUrl) => ({
  type: GET_CAPTCHA_URL_SUCCESS, userData: {
    captchaUrl
  }
})

export const getAuthorization = () => async (dispatch) => {
  const response = await authAPI.getAuth()
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    dispatch(setUserData(id, email, login, true));
  }
}

export const login = (email, password, rememberMe, captcha, setError) => async (dispatch) => {
  const response = await authAPI.login(email, password, rememberMe, captcha)
  if (response.data.resultCode === 0) {
    dispatch(getAuthorization())
  } else {
    if (response.data.resultCode === 10) {
      dispatch(getCaptchaUrl());
    }
    setError('serverError', { type: 'response', message: response.data.messages[0] })
  }
}

export const getCaptchaUrl = () => async (dispatch) => {
  const response = await securityAPI.getCaptchaUrl();
  const captchaUrl = response.data.url;

  dispatch(getCaptchaUrlSuccess(captchaUrl));
}

export const logout = () => async (dispatch) => {
  const response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(setUserData(null, null, null, false));
  }
}

